import { Fragment, useEffect, useState } from 'react'
import { Disclosure, Menu, Transition } from '@headlessui/react'
import { Bars3Icon, BellIcon, XMarkIcon } from '@heroicons/react/24/outline'
import { MoonIcon, SunIcon, UserIcon, ShoppingBagIcon } from '@heroicons/react/24/solid'
import { Link, NavLink } from 'react-router-dom'
import { useCart } from '../Providers/CartProvider'
import { useTheme, useThemeActions } from '../Providers/ThemeProvider'
import UserInfoMobile from './UserInfoMobile'
import { useAuthActions, useIsSignedIn } from '../Providers/AuthProvider'
import UserLoginMobile from './UserLoginMobile'

const navigation = [
    { name: 'Dashboard', to: '/', exact: true },
    { name: '404 Page', to: '/not-found' },
    { name: 'Checkout', to: '/checkout' },
    { name: 'Cart', to: '/cart' },
    { name: 'Developer Website', to: '/devsina' }
]

function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

export default function Navigation() {
    const { cart } = useCart()
    const theme = useTheme()
    const setTheme = useThemeActions()
    const themeChangeHandler = () => {
        if (theme === 'light') setTheme('dark')
        else setTheme('light')
    }
    const isLoggedIn = useIsSignedIn();
    const dispatch = useAuthActions()

    const signOut = () => {
        dispatch('')
        localStorage.removeItem('token')
    }
    const currencies = ['CAD', 'USD', 'AUD', 'EUR', 'GBP']

    return (
        <>
            {!isLoggedIn && (
                <nav aria-label="Top">
                    <div className="bg-gray-900 py-1.5 dark:bg-gray-600">
                        <div className="max-w-7xl mx-auto h-10 px-4 flex items-center justify-between sm:px-6 lg:px-8">
                            <form>
                                <div>
                                    <label htmlFor="desktop-currency" className="sr-only">
                                        Currency
                                    </label>
                                    <div className="-ml-2 group relative bg-gray-900 border-transparent rounded-md focus-within:ring-2 focus-within:ring-white">
                                        <select
                                            id="desktop-currency"
                                            name="currency"
                                            className="bg-none bg-gray-900 dark:bg-gray-600 dark:bg-gray-5 dark:text-white border-transparent rounded-md py-0.5 pl-2 pr-5 flex items-center text-sm font-medium text-white group-hover:text-gray-100 dark:group-hover:text-gray-300 focus:outline-none focus:ring-0 focus:border-transparent"
                                        >
                                            {currencies.map((currency) => (
                                                <option key={currency}>{currency}</option>
                                            ))}
                                        </select>
                                    </div>
                                </div>
                            </form>

                            <div className="flex items-center space-x-6">
                                <NavLink to="/login" className="font-semibold text-white hover:text-gray-100">
                                    Sign in
                                </NavLink>
                                <NavLink to="/signup" className="font-semibold text-white hover:text-gray-100">
                                    Create an account
                                </NavLink>
                            </div>
                        </div>
                    </div>
                </nav>
            )}
            <Disclosure as="nav" className="bg-white shadow dark:shadow-none dark:bg-gray-800">
                {({ open }) => (
                    <>
                        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                            <div className="flex justify-between h-16 dark:border-b border-[#374151]">
                                <div className="flex">
                                    <a className='inline-flex self-center h-8 w-auto' href="/">
                                        <img
                                            className="h-8"
                                            src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
                                            alt="Workflow"
                                        />
                                    </a>
                                    <div className="hidden sm:flex sm:ml-6">
                                        <div className="flex space-x-4 items-center">
                                            {navigation.map(n => (
                                                <NavLink
                                                    activeClassName='border-indigo-500 text-gray-900 inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium
                                            dark:bg-gray-900 dark:text-white'
                                                    className='border-transparent h-full text-gray-500 hover:border-gray-300 hover:text-gray-700 inline-flex items-center px-1 pt-1 border-b-2 text-sm font-medium
                                            dark:text-gray-300 dark:hover:bg-gray-700 dark:h-fit dark:hover:text-white dark:border-none dark:rounded-md dark:px-3 dark:py-2' {...n} key={n.to}>{n.name}</NavLink>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                                <div className="hidden sm:ml-6 sm:flex sm:items-center gap-x-3">
                                    <button onClick={themeChangeHandler} className='p-1.5 transition-all duration-300 focus:ring-4 focus:ring-[#f3f4f6] dark:focus:ring-[#374151] hover:bg-[#f3f4f6] dark:hover:bg-[#374151] cursor-pointer rounded-xl'>
                                        {theme === 'light' ? <MoonIcon className='w-6 h-6 text-[#6b7280]' /> : <SunIcon className='w-6 h-6 text-[#9ca3af]' />}

                                    </button>
                                    {isLoggedIn && (
                                        <Menu as="div" className="relative">
                                            <div>
                                                <Menu.Button className="p-1.5 text-[#6b7280] dark:text-[#9ca3af] transition-all duration-300 focus:ring-4 focus:ring-[#f3f4f6] dark:focus:ring-[#374151] hover:bg-[#f3f4f6] dark:hover:bg-[#374151] cursor-pointer rounded-xl">
                                                    <span className="sr-only">Open user menu</span>
                                                    <UserIcon className='h-6 w-6 rounded-full' />
                                                </Menu.Button>
                                            </div>
                                            <Transition
                                                as={Fragment}
                                                enter="transition ease-out duration-100"
                                                enterFrom="transform opacity-0 scale-95"
                                                enterTo="transform opacity-100 scale-100"
                                                leave="transition ease-in duration-75"
                                                leaveFrom="transform opacity-100 scale-100"
                                                leaveTo="transform opacity-0 scale-95"
                                            >
                                                <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white dark:bg-gray-700 py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                                                    <Menu.Item>
                                                        {({ active }) => (
                                                            <NavLink
                                                                to="/me"
                                                                className={classNames(active ? 'bg-gray-100 dark:bg-gray-600' : '', 'block px-4 py-2 text-sm text-gray-700 dark:text-gray-200 font-semibold')}
                                                            >
                                                                Your Profile
                                                            </NavLink>
                                                        )}
                                                    </Menu.Item>
                                                    <Menu.Item>
                                                        {({ active }) => (
                                                            <button
                                                                onClick={signOut}
                                                                to="/"
                                                                className={classNames(active ? 'bg-gray-100 dark:bg-gray-600' : '', 'block px-4 py-2 text-sm text-gray-700 dark:text-gray-200 font-semibold w-full text-left')}
                                                            >
                                                                Sign out
                                                            </button>
                                                        )}
                                                    </Menu.Item>
                                                </Menu.Items>
                                            </Transition>
                                        </Menu>
                                    )}
                                    <Link to='/cart'
                                        type="button"
                                        className="p-1.5 text-[#6b7280] dark:text-[#9ca3af] transition-all duration-300 focus:ring-4 focus:ring-[#f3f4f6] dark:focus:ring-[#374151] hover:bg-[#f3f4f6] dark:hover:bg-[#374151] cursor-pointer rounded-xl"
                                    >
                                        <span className="sr-only">View notifications</span>
                                        <div className='flex'>
                                            <ShoppingBagIcon className="h-6 w-6 relative mr-3" aria-hidden="true" />
                                            <span className='font-bold'>{cart.length}</span>
                                        </div>
                                    </Link>

                                </div>
                                <div className="-mr-2 flex items-center sm:hidden">
                                    {/* Mobile menu button */}
                                    <Disclosure.Button className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500
                                dark:hover:bg-gray-700 dark:focus:ring-white">
                                        <span className="sr-only">Open main menu</span>
                                        {open ? (
                                            <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                                        ) : (
                                            <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                                        )}
                                    </Disclosure.Button>
                                </div>
                            </div>
                        </div>

                        <Disclosure.Panel className="sm:hidden">
                            <div className="flex items-center px-4 gap-x-4 py-4 border-gray-300 border-y dark:border-t-0 dark:border-b mx-4 dark:border-[#374151]">
                                <Disclosure.Button
                                    as='button'
                                    onClick={themeChangeHandler}
                                    className='p-1.5 flex-1 flex justify-center transition-all duration-300 focus:ring-4 focus:ring-[#f3f4f6] dark:focus:ring-[#374151] hover:bg-[#f3f4f6] dark:hover:bg-[#374151] cursor-pointer rounded-xl'>
                                    {theme === 'light' ? <MoonIcon className='w-6 h-6 text-[#6b7280]' /> : <SunIcon className='w-6 h-6 text-[#9ca3af]' />}
                                </Disclosure.Button>
                                <Disclosure.Button
                                    as={Link}
                                    to='/cart'
                                    type="button"
                                    className="p-1.5 text-[#6b7280] dark:text-[#9ca3af] flex-1 flex justify-center transition-all duration-300 focus:ring-4 focus:ring-[#f3f4f6] dark:focus:ring-[#374151] hover:bg-[#f3f4f6] dark:hover:bg-[#374151] cursor-pointer rounded-xl">
                                    <span className="sr-only">View notifications</span>
                                    <div className='flex'>
                                        <ShoppingBagIcon className="h-6 w-6 relative mr-3" aria-hidden="true" />
                                        <span className='font-bold'>{cart.length}</span>
                                    </div>
                                </Disclosure.Button>
                            </div>
                            <div className="pt-2 pb-3 space-y-1 border-b border-gray-300 dark:border-[#374151]">
                                {/* Current: "bg-indigo-50 border-indigo-500 text-indigo-700", Default: "border-transparent text-gray-500 hover:bg-gray-50 hover:border-gray-300 hover:text-gray-700" */}
                                {navigation.map(n => (
                                    <Disclosure.Button
                                        as={NavLink}
                                        {...n}
                                        activeClassName='bg-indigo-50 border-indigo-500 text-indigo-700 block pl-3 pr-4 py-2 border-l-4 text-base font-medium
                                    dark:bg-gray-900 dark:text-white'
                                        className="border-transparent text-gray-500 hover:bg-gray-50 hover:border-gray-300 hover:text-gray-700 block pl-3 pr-4 py-2 border-l-4 text-base font-medium
                                    dark:text-gray-300 dark:hover:bg-gray-700"
                                        key={n.to}
                                    >
                                        {n.name}
                                    </Disclosure.Button>
                                ))}
                            </div>
                            {isLoggedIn ? <UserInfoMobile signOut={signOut} /> : <UserLoginMobile />}

                        </Disclosure.Panel>
                    </>
                )}
            </Disclosure>
        </>
    )
}
