const cartReducer = (state, action) => {
    const getTotal = (cart) => {
        let price = 0;
        const cartClone = [...cart]
        cartClone.map(c => price += (c.quantity * c.price))
        return price
    }
    switch (action.type) {
        case 'ADD_TO_CART': {
            const updatedCart = [...state.cart]
            const updatedItemIndex = updatedCart.findIndex(item => item.id === action.payload.id)

            if (updatedItemIndex < 0) {
                updatedCart.push({ ...action.payload, quantity: 1 })
            } else {
                const updatedItem = { ...updatedCart[updatedItemIndex] }
                updatedItem.quantity++;
                updatedCart[updatedItemIndex] = updatedItem;
            }

            return { ...state, cart: updatedCart, total: getTotal(updatedCart) }
        }
        case 'REMOVE_PRODUCT': {
            const updatedCart = [...state.cart]
            const updatedItemIndex = updatedCart.findIndex(item => item.id === action.payload.id)

            const updatedItem = { ...updatedCart[updatedItemIndex] }

            if (updatedItem.quantity === 1) {
                const filteredCart = updatedCart.filter(p => p.id !== action.payload.id)
                return { ...state, cart: filteredCart, total: getTotal(filteredCart) }
            } else {
                updatedItem.quantity--;
                updatedCart[updatedItemIndex] = updatedItem;
                return { ...state, cart: updatedCart, total: getTotal(updatedCart) }
            }

        }
        case 'UPDATE_PRODUCT': {
            const updatedCart = [...state.cart]
            const updatedItemIndex = updatedCart.findIndex(item => item.id === action.payload.id)
            const updatedItem = { ...updatedCart[updatedItemIndex] }
            updatedItem.quantity = action.payload.quantity;
            updatedCart[updatedItemIndex] = updatedItem

            return { ...state, cart: updatedCart, total: getTotal(updatedCart) }
        }
        default: return state;
    }
}

export default cartReducer