import { CheckIcon, ClockIcon } from '@heroicons/react/24/solid'
import { useEffect } from 'react';
import { useCart, useCartActions } from '../Providers/CartProvider';

const CartProduct = (product) => {
    const state = useCart();
    const cart = state.cart;
    const dispatch = useCartActions()

    const clickHandler = () => {
        dispatch({ type: 'REMOVE_PRODUCT', payload: product })
    }

    const selectHandler = (e) => {
        const clonedProduct = { ...product }
        clonedProduct.quantity = Number(e.target.value)
        dispatch({ type: 'UPDATE_PRODUCT', payload: clonedProduct })
    }

    return (
        <li key={product.id} className="flex py-6 sm:py-10">
            <div className="flex-shrink-0">
                <img
                    src={product.imageSrc}
                    alt={product.imageAlt}
                    className="w-24 h-24 rounded-lg object-center object-cover sm:w-32 sm:h-32"
                />
            </div>

            <div className="relative ml-4 flex-1 flex flex-col justify-between sm:ml-6">
                <div>
                    <div className="flex justify-between sm:grid sm:grid-cols-2">
                        <div className="pr-6">
                            <h3 className="text-sm">
                                <p href={product.href} className="font-bold text-lg cursor-auto text-gray-700 dark:text-gray-100">
                                    {product.name}
                                </p>
                            </h3>
                            <p className="mt-1 text-sm text-gray-500 dark:text-gray-300 font-bold">{product.color}</p>
                            {product.size ? <p className="mt-1 text-sm text-gray-500">{product.size}</p> : null}
                        </div>

                        <p className="text-xl font-bold dark:text-gray-100 text-gray-900 text-right">${product.price}</p>
                    </div>

                    <div className="mt-4 flex items-center sm:block sm:absolute sm:top-0 sm:left-1/2 sm:mt-0">
                        <label htmlFor={`quantity-${product.quantity}`} className="sr-only">
                            Quantity, {product.name}
                        </label>
                        <select
                            id={`quantity-${product.quantity}`}
                            name={`quantity-${product.quantity}`}
                            value={product.quantity}
                            onChange={(e) => selectHandler(e)}
                            className="block max-w-full rounded-md border border-gray-300 dark:border-gray-900 py-1.5 text-base leading-5 font-medium text-gray-700 text-left shadow-sm focus:outline-none focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm px-4
                            dark:focus:ring-white dark:bg-gray-600 dark:text-gray-100 dark:focus:border-white"
                        >
                            <option value={1}>1</option>
                            <option value={2}>2</option>
                            <option value={3}>3</option>
                            <option value={4}>4</option>
                            <option value={5}>5</option>
                            <option value={6}>6</option>
                            <option value={7}>7</option>
                            <option value={8}>8</option>
                        </select>

                        <button
                            type="button"
                            className="ml-4 text-sm font-medium text-indigo-600 hover:text-indigo-500 sm:ml-0 sm:mt-3"
                            onClick={clickHandler}
                        >
                            <span>Remove</span>
                        </button>
                    </div>
                </div>

                <p className="mt-4 flex text-sm text-gray-700 dark:text-gray-300 font-semibold space-x-2">
                    {product.inStock ? (
                        <CheckIcon className="flex-shrink-0 h-5 w-5 text-green-500" aria-hidden="true" />
                    ) : (
                        <ClockIcon className="flex-shrink-0 h-5 w-5 text-gray-300" aria-hidden="true" />
                    )}

                    <span>{product.inStock ? 'In stock' : `Ships in ${product.leadTime}`}</span>
                </p>
            </div>
        </li>
    );
}

export default CartProduct;