import { useCart, useCartActions } from "../Providers/CartProvider";
import toast from 'react-hot-toast'

const Product = (product) => {
    const { cart } = useCart()
    const dispatch = useCartActions()
    const clickHandler = () => {
        toast.custom(t => {
            return (
                <div id="toast-success" className="flex items-center p-4 mb-4 w-full max-w-xs text-gray-500 bg-white rounded-lg shadow dark:text-gray-400 dark:bg-gray-700" role="alert">
                    <div className="inline-flex flex-shrink-0 justify-center items-center w-8 h-8 text-green-500 bg-green-100 rounded-lg dark:bg-green-800 dark:text-green-200">
                        <svg aria-hidden="true" className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd"></path></svg>
                        <span className="sr-only">Check icon</span>
                    </div>
                    <div className="ml-3 text-sm font-normal">Item moved successfully.</div>
                    <button onClick={() => toast.dismiss(t.id)} type="button" className="ml-auto -mx-1.5 -my-1.5 bg-white text-gray-400 hover:text-gray-900 rounded-lg focus:ring-2 focus:ring-gray-300 p-1.5 hover:bg-gray-100 inline-flex h-8 w-8 dark:text-gray-500 dark:hover:text-white dark:bg-gray-700 dark:hover:bg-gray-800" data-dismiss-target="#toast-success" aria-label="Close">
                        <span className="sr-only">Close</span>
                        <svg aria-hidden="true" className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
                    </button>
                </div>
            )
        })
        dispatch({
            type: 'ADD_TO_CART',
            payload: product
        })
    }
    const checkInCart = () => {
        return cart.find(c => c.id === product.id)
    }

    return (
        <div key={product.id}>
            <div className="relative">
                <div className="relative w-full h-72 rounded-lg overflow-hidden">
                    <img
                        src={product.imageSrc}
                        alt={product.imageAlt}
                        className="w-full h-full object-center object-cover"
                    />
                </div>
                <div className="relative mt-4">
                    <h3 className="text-sm sm:text-lg font-bold text-gray-900 dark:text-gray-100">{product.name}</h3>
                    <p className="mt-1 text-sm sm:text-md text-gray-500 dark:text-gray-300 font-semibold">{product.color}</p>
                </div>
                <div className="absolute top-0 inset-x-0 h-72 rounded-lg p-4 flex items-end justify-end overflow-hidden">
                    <div
                        aria-hidden="true"
                        className="absolute inset-x-0 bottom-0 h-36 bg-gradient-to-t from-black opacity-50"
                    />
                    <p className="relative text-xl font-semibold text-white">${product.price}</p>
                </div>
            </div>
            <div className="mt-6">
                <button
                    onClick={clickHandler}
                    disabled={checkInCart()}
                    className="relative disabled:cursor-not-allowed font-bold disabled:hover:bg-red-500 dark:disabled:hover:bg-red-500 flex cursor-pointer w-full bg-gray-200 border border-transparent rounded-md py-2 px-8 items-center justify-center text-sm text-gray-900 hover:bg-gray-300 dark:text-gray-100 dark:bg-gray-700 dark:hover:bg-gray-600"
                >
                    {checkInCart() ? 'In bag' : 'Add to bag'}<span className="sr-only">, {product.name}</span>
                </button>
            </div>
        </div>
    );
}

export default Product;