import { XCircleIcon } from "@heroicons/react/24/solid";
import { useFormik } from "formik";
import { useState } from "react";
import { Link, withRouter } from "react-router-dom";
import * as Yup from 'yup'
import signInUser from "../services/signInService";
import { useAuthActions, useIsSignedIn } from '../Providers/AuthProvider'
import queryString from 'query-string'

const LoginPage = ({ history }) => {
    const loggedIn = useIsSignedIn()
    if (loggedIn) history.push('/')
    const {redirect} = queryString.parse(window.location.search);

    const dispatch = useAuthActions()

    const [error, setError] = useState(0)

    const formik = useFormik({
        initialValues: {
            email: "",
            password: "",
            remember: ""
        },
        onSubmit: (values) => onSubmit(values),
        validationSchema: Yup.object({
            email: Yup.string().email('Enter Vaild Email!').required('Email is Required'),
            password: Yup.string().required('Password is Required'),
            remember: Yup.boolean()
        }),
        validateOnMount: true
    })

    const onSubmit = async ({ email, password }) => {
        const userData = {
            email,
            password
        }

        try {
            const { data } = await signInUser(userData)
            const token = data['x-auth-token']
            dispatch({ token: token })
            localStorage.setItem('token', JSON.stringify(token))
            setError('')
            history.push(`/${redirect ? redirect : ''}`)
        } catch (ex) {
            setError(ex.response.data.message)
        }
    }
    return (
        <>
            <div className="min-h-full flex flex-col justify-center py-12 sm:px-6 lg:px-8">
                <div className="sm:mx-auto sm:w-full sm:max-w-md">
                    <img
                        className="mx-auto h-12 w-auto"
                        src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
                        alt="Workflow"
                    />
                    <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900 dark:text-gray-100">Sign in to your account</h2>
                    <p className="mt-2 text-center text-sm text-gray-600 dark:text-gray-300 font-semibold">
                        Or{' '}
                        <Link to={`/signup${redirect ? '?redirect=' + redirect : ''}`} className="font-medium text-indigo-600 hover:text-indigo-500">
                            Sign Up your account now
                        </Link>
                    </p>
                </div>

                <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md px-4">
                    {error ?
                        <div className="rounded-md bg-red-100 dark:bg-red-50 p-4 mb-4">
                            <div className="flex">
                                <div className="flex-shrink-0">
                                    <XCircleIcon className="h-5 w-5 text-red-400" aria-hidden="true" />
                                </div>
                                <div className="ml-3">
                                    <h3 className="text-sm font-medium text-red-800">{error}</h3>
                                </div>
                            </div>
                        </div> :
                        ''
                    }
                    <form onSubmit={formik.handleSubmit}>
                        <div className="mb-6">
                            <label htmlFor="email" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Your email</label>
                            <input
                                {...formik.getFieldProps('email')}
                                type="email" id="email" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.email && formik.touched.email && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} placeholder="example@example.com" required />
                            {formik.errors.email && formik.touched.email && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.email}</p>}
                        </div>
                        <div className="mb-6">
                            <label htmlFor="password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Your password</label>
                            <input
                                {...formik.getFieldProps('password')}
                                type="password" id="password" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.password && formik.touched.password && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} required />
                            {formik.errors.password && formik.touched.password && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.password}</p>}
                        </div>
                        <div className="flex items-start mb-6">
                            <div className="flex items-center h-5">
                                <input
                                    {...formik.getFieldProps('remember')}
                                    id="remember" type="checkbox" value="" className="w-4 h-4 bg-gray-50 rounded border border-gray-300 focus:ring-3 focus:ring-blue-300 dark:bg-gray-700 dark:border-gray-600 dark:focus:ring-blue-600 dark:ring-offset-gray-800" />
                            </div>
                            <label htmlFor="remember" className="ml-2 text-sm font-medium text-gray-900 dark:text-gray-300">Remember me</label>
                        </div>
                        <button disabled={!formik.isValid} type="submit" className="text-white disabled:!bg-gray-500 disabled:cursor-not-allowed bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Submit</button>
                    </form>
                </div>
            </div>
        </>
    );
}

export default withRouter(LoginPage);