import { useContext } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { createContext, useReducer } from "react";
import getMeUser from "../services/getMeService";

const AuthContext = createContext()
const isLoggedIn = createContext()
const AuthContextDispatcher = createContext()
const initialState = { token: JSON.parse(localStorage.getItem('token')) } || null

const AuthProvider = ({ children }) => {

    const [auth, dispatch] = useState(initialState)
    const [signedIn, setSignedIn] = useState(false)

    useEffect(() => {
        async function validateToken() {
            try {
                const data = await getMeUser({
                    headers: {
                        'x-auth-token': auth.token
                    }
                })
                setSignedIn(true)
            } catch {
                setSignedIn(false)
            }
        }
        validateToken()
    }, [auth])

    return (
        <AuthContext.Provider value={auth}>
            <isLoggedIn.Provider value={signedIn}>
                <AuthContextDispatcher.Provider value={dispatch}>
                    {children}
                </AuthContextDispatcher.Provider>
            </isLoggedIn.Provider>
        </AuthContext.Provider>
    );
}

export default AuthProvider;

export const useAuth = () => useContext(AuthContext)
export const useIsSignedIn = () => useContext(isLoggedIn)
export const useAuthActions = () => useContext(AuthContextDispatcher)