import { useFormik } from "formik";
import { useState } from "react";
import { Link, withRouter } from "react-router-dom";
import * as Yup from 'yup'
import signUpUser from '../services/signUpService'
import { XCircleIcon } from '@heroicons/react/24/solid'
import { useAuthActions, useIsSignedIn } from "../Providers/AuthProvider";
import queryString from 'query-string'

const SignUpPage = ({ history }) => {
    const loggedIn = useIsSignedIn()
    if (loggedIn) history.push('/')
    const { redirect } = queryString.parse(window.location.search);

    const dispatch = useAuthActions()
    const [error, setError] = useState(0)

    const onSubmit = async ({ name, email, number, password }) => {
        let numberS = number.toString()
        const userData = {
            name,
            email,
            phoneNumber: numberS,
            password
        }

        try {
            const { data } = await signUpUser(userData)
            const token = data['x-auth-token']
            dispatch({ token: token })
            localStorage.setItem('token', JSON.stringify(token))
            setError('')
            history.push(`/${redirect}`)
        } catch (ex) {
            setError(ex.response.data.message)
        }
    }

    const formik = useFormik({
        initialValues: {
            name: "",
            email: "",
            number: "",
            password: "",
            passwordConfirm: "",
            terms: ""
        },
        onSubmit: (values) => console.log(values),
        validationSchema: Yup.object({
            name: Yup.string().required('First Name is Required').min(5, 'Name must be 5 chars or more.'),
            email: Yup.string().email('Enter Vaild Email!').required('Email is Required'),
            number: Yup.string().required('Phone Number is Required')
                .matches(/^[0-9]{10}$/, 'Invaild Phone Number')
            ,
            password: Yup.string().required('Password is Required').matches(
                /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
                "Must Contain 8 Characters, One Uppercase, One Lowercase, One Number and One Special Case Character"
            ),
            passwordConfirm: Yup.string()
                .required('Passwords must match')
                .oneOf([Yup.ref('password'), null], 'Passwords must match'),
            terms: Yup.boolean().required('Please agree with terms to sign up').oneOf([true], 'Please agree with terms to sign up')
        }),
        validateOnMount: true
    })

    return (
        <>
            <div className="min-h-full flex flex-col justify-center py-12 px-4 sm:px-6 lg:px-8">
                <div className="sm:mx-auto sm:w-full sm:max-w-md">
                    <img
                        className="mx-auto h-12 w-auto"
                        src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
                        alt="Workflow"
                    />
                    <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900 dark:text-gray-100">Sign up your account</h2>
                    <p className="mt-2 text-center text-sm text-gray-600 dark:text-gray-300 font-semibold">
                        Or{' '}
                        <Link to={`/login${redirect ? '?redirect=' + redirect : ''}`} className="font-medium text-indigo-600 hover:text-indigo-500">
                            Login to your account
                        </Link>
                    </p>
                    <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
                        {error ?
                            <div className="rounded-md bg-red-50 p-4 mb-4">
                                <div className="flex">
                                    <div className="flex-shrink-0">
                                        <XCircleIcon className="h-5 w-5 text-red-400" aria-hidden="true" />
                                    </div>
                                    <div className="ml-3">
                                        <h3 className="text-sm font-medium text-red-800">{error}</h3>
                                    </div>
                                </div>
                            </div> :
                            ''
                        }
                        <form onSubmit={(e) => {
                            formik.handleSubmit(e)
                            onSubmit(formik.values)
                        }}>
                            <div className="mb-6">
                                <label htmlFor="name" className='block mb-2 text-sm font-medium text-gray-900 dark:text-white'>Your Name</label>
                                <input
                                    {...formik.getFieldProps('name')}
                                    type="name" id="name" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.name && formik.touched.name && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} placeholder="Your Name" required />
                                {formik.errors.name && formik.touched.name && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.name}</p>}
                            </div>
                            <div className="mb-6">
                                <label htmlFor="email" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Your email</label>
                                <input
                                    {...formik.getFieldProps('email')}
                                    type="email" id="email" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.email && formik.touched.email && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} placeholder="example@example.com" required />
                                {formik.errors.email && formik.touched.email && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.email}</p>}
                            </div>
                            <div className="mb-6">
                                <label htmlFor="number" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Your number</label>
                                <input
                                    {...formik.getFieldProps('number')}
                                    type="number" id="number" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.number && formik.touched.number && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} placeholder="0901234567" required />
                                {formik.errors.number && formik.touched.number && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.number}</p>}
                            </div>
                            <div className="mb-6">
                                <label htmlFor="password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Your password</label>
                                <input
                                    {...formik.getFieldProps('password')}
                                    type="password" id="password" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.password && formik.touched.password && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} required />
                                {formik.errors.password && formik.touched.password && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.password}</p>}
                            </div>
                            <div className="mb-6">
                                <label htmlFor="repeat-password" className="block mb-2 text-sm font-medium text-gray-900 dark:text-white">Repeat password</label>
                                <input
                                    {...formik.getFieldProps('passwordConfirm')}
                                    type="password" id="repeat-password" className={`shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500 dark:shadow-sm-light ${formik.errors.passwordConfirm && formik.touched.passwordConfirm && 'g-red-50 border !text-red-900 border-red-500 placeholder-red-700 text-sm rounded-lg focus:ring-red-500 focus:border-red-500 block w-full p-2.5 dark:bg-red-100 dark:border-red-400'}`} required />
                                {formik.errors.passwordConfirm && formik.touched.passwordConfirm && <p className="mt-2 text-sm text-red-600 dark:text-red-500">{formik.errors.passwordConfirm}</p>}
                            </div>
                            <div className="flex items-start">
                                <div className="flex items-center h-5">
                                    <input
                                        {...formik.getFieldProps('terms')}
                                        id="terms" type="checkbox" value="" className={`w-4 h-4 bg-gray-50 rounded border border-gray-300 focus:ring-3 focus:ring-blue-300 dark:bg-gray-700 dark:border-gray-600 dark:focus:ring-blue-600 dark:ring-offset-gray-800 ${formik.errors.terms && formik.touched.terms && 'ring-2 ring-red-500 border-red-500'}`} required />
                                </div>
                                <label htmlFor="terms" className="ml-2 text-sm font-medium text-gray-900 dark:text-gray-300">I agree with the <a href="#" className="text-blue-600 hover:underline dark:text-blue-500">terms and conditions</a></label> <br />
                            </div>
                            {formik.errors.terms && formik.touched.terms && <p className="mt-2 text-sm mb-6 text-red-600 dark:text-red-500">{formik.errors.terms}</p>}
                            <button disabled={!formik.isValid} type="submit" className="text-white disabled:!bg-gray-500 disabled:cursor-not-allowed bg-blue-700 mt-6 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Register new account</button>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}

export default withRouter(SignUpPage);