import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { useIsSignedIn } from "../Providers/AuthProvider";
import { useCart } from "../Providers/CartProvider";
import CartProduct from "./CartProduct";

const CartProducts = () => {
    const isLoggedIn = useIsSignedIn()
    const { cart, total } = useCart()

    return (
        <div>
            <div className="max-w-4xl mx-auto py-10 px-4 sm:py-12 sm:px-6 lg:px-8">
                <h1 className="text-3xl font-bold dark:text-gray-100 tracking-tight text-gray-900">Shopping Cart</h1>

                <form className="mt-12">
                    <div>
                        <h2 className="sr-only">Items in your shopping cart</h2>

                        <ul role="list" className="border-t border-b border-gray-200 divide-y divide-gray-200">
                            {cart.map(p => <CartProduct key={p.id} {...p} />)}
                        </ul>
                    </div>

                    {/* Order summary */}
                    <div className="mt-10 sm:ml-32 sm:pl-6">
                        <div className="bg-gray-50 dark:bg-gray-700 rounded-lg px-4 py-6 sm:p-6 lg:p-8">
                            <h2 className="sr-only">Order summary</h2>

                            <div className="flow-root">
                                <dl className="-my-4 text-sm divide-y divide-gray-200">
                                    <div className="py-4 flex items-center justify-between">
                                        <dt className="text-gray-600 dark:text-gray-100">Subtotal</dt>
                                        <dd className="text-gray-900 dark:text-gray-300 font-semibold">${total}</dd>
                                    </div>
                                    <div className="py-4 flex items-center justify-between">
                                        <dt className="text-gray-600 dark:text-gray-100">Shipping</dt>
                                        <dd className="text-gray-900 dark:text-gray-300 font-semibold">{total ? '$5.00' : '$0'}</dd>
                                    </div>
                                    <div className="py-4 flex items-center justify-between">
                                        <dt className="text-gray-600 dark:text-gray-100">Tax</dt>
                                        <dd className="text-gray-900 dark:text-gray-300 font-semibold">{total ? '$8.32' : '$0'}</dd>
                                    </div>
                                    <div className="py-4 flex items-center justify-between">
                                        <dt className="text-base font-medium text-gray-900 dark:text-gray-300">Order total</dt>
                                        <dd className="text-base font-medium text-gray-900 dark:text-gray-300">${total ? total + 5.00 + 8.32 : 0}</dd>
                                    </div>
                                </dl>
                            </div>
                        </div>
                        <div className="mt-10">
                            <NavLink
                                to={isLoggedIn ? '/checkout' : '/login?redirect=checkout'}
                                type="submit"
                                className="w-full text-center bg-indigo-600 border border-transparent rounded-md shadow-sm py-3 px-4 text-base font-medium text-white hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-50 focus:ring-indigo-500"
                            >
                                Checkout
                            </NavLink>
                        </div>

                        <div className="mt-6 text-sm text-center text-gray-500">
                            <p>
                                or{' '}
                                <NavLink to="/" className="text-indigo-600 font-medium hover:text-indigo-500">
                                    Continue Shopping<span aria-hidden="true"> &rarr;</span>
                                </NavLink>
                            </p>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default CartProducts;